<template>
  <div class="professionalTask">
    <div class="tool-bar">
      <div class="btn-group">
        <el-button type="primary" size="small" @click="showAddForm(true)">
          <i class="el-icon-plus"></i>
          添加
        </el-button>
        <el-button type="danger" size="small">
          <i class="el-icon-delete"></i>
          删除
        </el-button>
      </div>
    </div>
    <pro-task-table />
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="pageSize"
        :total="count"
        layout="total, sizes, prev, pager, next, jumper"
      ></el-pagination>
    </div>
    <addForm :addFormVisible="addFormVisible" @show="showAddForm" :addForm="addFormData" />
  </div>
</template>

<script>
import proTaskTable from "./proTaskTable";
import addForm from "./addForm";
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  components: {
    proTaskTable,
    addForm
  },
  data() {
    return {
      addFormData: {
        start_time: "",
        end_time: "",
        grade_id: "",
        major_id: ""
      },
      addFormVisible: false
    };
  },
  mounted() {
    this.findByPage({ page: this.currentPage, pageSize: this.pageSize });
  },
  computed: {
    ...mapState("task", ["currentPage", "pageSize", "count"])
  },
  methods: {
    ...mapMutations("task", ["setPage", "setPageSize"]),
    ...mapActions("task", ["findByPage"]),
    handleSizeChange(val) {
      this.setPageSize(val);
    },
    handleCurrentChange(val) {
      this.setPage(val);
    },
    showAddForm(isShow) {
      this.addFormVisible = isShow;
    }
  }
};
</script>

