import axios from 'axios';
import qs from 'qs';

const loginUser = {
    queryUserByLoginId(params) {
        const data = qs.stringify(params);
        return axios({
            url: '/api/login',
            method: 'post',
            headers: {
                "Content-Type": 'application/x-www-form-urlencoded; charset=UTF-8'
            },
            data
        });
    },
    queryUserMsg(params) {
        const data = qs.stringify(params);
        return axios({
            url: '/api/login/queryUser',
            method: 'post',
            headers: {
                "Content-Type": 'application/x-www-form-urlencoded; charset=UTF-8'
            },
            data
        });
    },
    changePwd(params) {
        const data = qs.stringify(params);
        return axios({
            url: '/api/login/changePwd',
            method: 'post',
            headers: {
                "Content-Type": 'application/x-www-form-urlencoded; charset=UTF-8'
            },
            data
        });
    }
}

export default loginUser;
