import axios from 'axios';
import qs from 'qs';
const url = {
    findByPage: '/api/task/findByPage',
    addTask: '/api/task/addTask',
    delTasks: '/api/task/delTasks',
    updateTask: '/api/task/updateTask'
}
const task = {
    findByPage(params) {
        return axios.get(url.findByPage, { params });
    },
    addTask(params) {
        const data = JSON.stringify(params);
        return axios({
            url: url.addTask,
            method: 'post',
            headers: {
                "Content-Type": 'application/json; charset=UTF-8'
            },
            data
        });
    },
    delTasks(params) {
        const data = JSON.stringify(params);
        return axios({
            url: url.delTasks,
            method: 'delete',
            headers: {
                "Content-Type": 'application/json; charset=UTF-8'
            },
            data
        });
    },
    updateTask(params) {
        const data = qs.stringify(params);
        return axios({
            url: url.updateTask,
            method: 'post',
            headers: {
                "Content-Type": 'application/json; charset=UTF-8'
            },
            data
        });
    }
}

export default task;