import api from '@/api';
export default {
    namespaced: true,
    state: {
        user: null
    },
    getter: {},
    mutations: {
        setUser(state, user) {
            state.user = user;
        }
    },
    actions: {
        queryUserMsg({commit}, form) {
            api.loginUser.queryUserMsg(form).then(res => {
                commit('setUser', res.data[0]);
            })
        }
    }
}