import api from '@/api';
import { Message } from 'element-ui';
export default {
    namespaced: true,
    state: {
        multipleSelection: [],
        currentPage: 1,
        pageSize: 10,
        count: 0,
        taskList: []
    },
    getter: {},
    mutations: {
        setSelection(state, val) {
            state.multipleSelection = val;
        },
        setPage(state, page) {
            state.page = page;
        },
        setPageSize(state, pageSize) {
            state.pageSize = pageSize;
        },
        setCount(state, count) {
            state.count = count;
        },
        setTaskList(state, data) {
            state.taskList = data;
        }
    },
    actions: {
        findByPage({ commit }, obj) {
            api.task.findByPage(obj).then(res => {
                commit('setCount', res.data.count);
                commit('setTaskList', res.data.data);
            });
        },
        addTask({ state, dispatch }, task) {
            console.log(task);
            api.task.addTask(task).then(res => {
                Message({
                    message: res.data.msg,
                    type: "success",
                    duration: 2000,
                    center: true
                });
                dispatch('findByPage', { page: state.currentPage, pageSize: state.pageSize });
            })
        },
        delTasks({ state, dispatch }, tasks) {
            api.task.delTasks({ tasks }).then(res => {
                Message({
                    message: res.data.msg,
                    type: "success",
                    duration: 2000,
                    center: true
                });
                dispatch('findByPage', { page: state.currentPage, pageSize: state.pageSize });
            })
        },
        updateTask({ state, dispatch }, editForm) {
            api.task.updateTask(editForm).then(res => {
                Message({
                    message: res.data.msg,
                    type: "success",
                    duration: 2000,
                    center: true
                });
                dispatch('findByPage', { page: state.currentPage, pageSize: state.pageSize });
            });
        }
    }
}